@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #333;
  font-family: 'Caveat', cursive;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

img.logo {
  margin: 0.2% 5% 0.2% 7%;
}

nav.navbar {
  background-color: rgb(32, 32, 32);
  color: white;
}

div.collapse > ul.navbar-nav > li.nav-item.active > a.nav-link {
  margin: 0 20px;
  transition: all 300ms;
  font-family: 'Ubuntu', sans-serif;
  color: white;
}

div.collapse > ul.navbar-nav > li.nav-item.active > a.nav-link > span {
  display: inline;
  padding: 2%;
}


span.nav-link#navbarDropdown {
  font-family: 'Ubuntu', sans-serif;
  color: white
}

span.nav-link#navbarDropdown > span{
  font-family: 'Ubuntu', sans-serif;
  font-size: 1.3em;
  margin-left: 2%;
  padding-top: 40%;
}

span.nav-link#navbarDropdown:hover {
  transition: all 300ms;
}

div.collapse > ul.navbar-nav > li.nav-item.active > a.nav-link:hover {
  transform: scale(1.03);
  color: rgb(0, 0, 197);
}

span.nav-link#navbarDropdown:hover {
  transform: scale(1.03);
  color: rgb(0, 0, 197);
}

div.dropdown-menu > a.dropdown-item {
  transition: all 300ms;
  font-size: 1.2em;
  font-family: "Ubuntu", sans-serif;
}

div.dropdown-menu > a.dropdown-item:hover {
  font-weight: 900;
  color: rgb(0, 0, 197);
}

.pokemon {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 1%;
  border-top: 0.5px solid #e4c439;
  border-bottom: 0.5px solid #e4c439;
  border-radius: 10px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  background-color: #444;
  position: relative;
  padding: 0;
  width: 150px;
  height: 200px;
  text-decoration: none;
  color: rgba(238, 238, 238, 0.925);
  text-transform: capitalize;
}

.pokemon:hover {
  cursor: pointer;
  transform: scale(1.03);
  transition: all 0.2s;
  background-color: rgba(131, 131, 131, 0.979);
  color: white;
  box-shadow: 1px 1px 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  font-size: 1.05rem;
  text-decoration: none;
}

h3.pokemon:active {
  cursor: pointer;
  transform: scale(0.95);
  transition: all 0.2s;
  background-color: rgba(131, 131, 131, 0.979);
  color: white;
  font-size: 1.05rem;
  text-decoration: none;
}

.pokemon-id {
  background: #cebf7b;
  width: 3rem;
  color: #333;
  padding: 0.1rem;
  font-weight: 700;
  align-self: flex-start;
  margin-top: -18px;
  border-radius: 10px 0 10px 0;
  top: 0px;
  left: 0;
}

.pokemon-id:hover {
  color: white;
  text-decoration: none;
}

.pokedex {
  display: flex;
  flex-flow: wrap;
  margin: 0 auto;
  margin-top: 0.5%;
  padding: 5%;
  align-items: center;
  justify-content: center;
}

.content {
  padding: 2% 5%;
}

.content span.link-to-back-text {
  font-family: "Ubuntu", sans-serif;
  font-size: 1.5em;
  text-decoration: none;
  margin-left: 2%;
}


.content div.link-to-back {
  transition: all 300ms;
}

.content div.link-to-back:hover {
  transform: scale(1.02);
  font-family: "Ubuntu", sans-serif;
  color: aqua;
  text-decoration: none;
  border-bottom: none;
}

.pokemon-name {
  text-transform: capitalize;
}

span.type-name {
  text-transform: capitalize;
}


